 

 .footer {
    padding: 20px 15px 0px 15px;
    margin-top: 50px;
    background-color: #ecdbcb;
    color: #93533c;
    flex-direction: column;
    font-family: gifthub-font;
 }

 .footer div {
    justify-content: center;
    display: flex;
    flex-direction: column;
    align-items: center;
 
 }
 .heading {
    font-weight: bold;
    margin-bottom: 10px;
    font-size: 1.5rem;
 }
 .content {
    padding: 0px 15% 0px 15%;
    text-align: center;
    font-size: 1rem !important;
 }
 .contact-us {
    margin-top: 10px;
 }

 .right-section {
   background-color: #93533c;
   margin-left:-15px ;
   margin-right: -15px;
   padding: 10px;
   backdrop-filter: blur(10px);
   color: white;
   font-family: monospace;
   display: inline !important;
   text-align: center;
   border-radius: 15px 15px 0px 0px;
 }

 a {
   padding: 0;
   margin: 0;
   color: inherit;
   text-decoration: underline;
   
 }
 