

.tab-container {
    display: flex;
    flex-direction: column;
}

.tab-container .menu {
    display: flex;
}

.menu-item {
    display: flex;
    justify-content: center;
    flex: 1;
    padding: 10px;
    border-bottom: 1px solid #9e9e9e;
}

.menu-item h6 {
    font-size: 16px;
    color: #9e9e9e;
}

.menu-item.selected {
    border-bottom: 1px solid black !important;
    color: var(--primary-color);
}

.tab-container  .content {
 padding: 15px;
}

.content {
    display: flex;
    min-height:100px;
}
.content div {
    display: flex;
    justify-content: flex-start;
    text-align: left;
}