@font-face {
  font-family: gifthub-font-1;
  src: url(https://cdn.jsdelivr.net/gh/tarunrawat87/gifthub-backend/static/static/media/Sacramento-Regular.ttf);
}
@font-face {
  font-family: gifthub-font;
  src: url(https://cdn.jsdelivr.net/gh/tarunrawat87/gifthub-backend/static/static/media/MSUIGHUR.TTF);
}

:root {
  --primary-color: #93533c  
}

html,body,#root {
  height: 100%;
  width: 100%;
  overflow-x: hidden;
  font-style: gifthub-font;
}

div {
  box-sizing: border-box;
}

#root {
  display: flex;
  flex-direction: column;
  height: 100%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 ,h2 ,h3 ,h4 ,h5 ,h6 ,p {
  padding: 0;
  margin: 0px;
}

p {
  margin: 0px;
}

.flx {
  display: flex;
}

.flx-col {
  display: flex;
  flex-direction: column;
}

.hamburger {
  width: 30px;
}

div.hamburger > div {
  height: 3px;
  background-color: black;
  margin: 3px 0;
}

.header {
  padding: 15px;
  justify-content: space-between;
  flex-direction: column;
}

.header-main-menu {
display: flex;
justify-content: space-between;
margin-bottom: 15px;
}

.logo {
  justify-content: center;
  align-items: center;
 
}

.logo > h1 {
  font-size: 1.5rem;
  color: #93533c;
  font-style: poppins, sans-serif;
  margin: 0px;
}

.header-sub-menu {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color:  #93533c;
  border-radius: 10px 10px 0px 0px;
  padding: 10px;
  transition: height ease-out 2s;
  color: #ecdbcb;
  transition: height 1s ease, padding 1s ease, display 1s ease;
}

.header-sub-menu div {
  flex: 1;
  padding: 10px;
  min-height: 0px;
  transition: line-height 1s, overflow 1s ;
}
.header-sub-menu p {
  font-size: 1.2rem;
  min-height: 0px;
}
.collapse {
  line-height: 0px;
  padding: 0px !important;
  height: 0px;
  overflow: hidden;
}

.carousal-container {
  padding: 0px 15px 0px 15px;
  width: 100%;
  flex-direction: column;
}

.image-container {
  height: 60vh;
  flex: 1;
}

.carousal-container img{
  max-height: 100%;
  max-width: 100%;
  flex: 1;
}

.header-sub-menu .expand {
  height: 180px;
}

.dot {
  cursor: pointer;
  height: 15px;
  width: 15px;
  margin: 0 2px;
  background-color: #93533c;
  opacity: .4;
  border-radius: 50%;
  display: inline-block;
  transition: background-color 0.6s ease;
}
.divider {
  display: flex;
  justify-content: center;
  margin-top: 10px;
}
.divider-container {

}

.fade {
  animation-name: fade;
  animation-duration: 2s;
}

.hide{
  display: none !important;
}

.cat-header {
  align-items: center;
  justify-content: center;
  margin-top: 20px;
  margin-bottom: 10px;
   
}

.cat-header span {
  font-size: 2.5rem;
  color: #93533c;
  font-weight: bold;
  font-family: gifthub-font;
}

.cat-container {
  flex-wrap: wrap;
  gap: 10px;
}


.cat-inner {
  height: auto;
  flex: 1;
  min-width: 250px;
  margin: 10px;
  position: relative;
}
.cat-inner img {
  max-width: 100%;
  max-height: 100%;
  flex: 1;
}

.cat-outer {
  flex: 1;
  padding: 10px;
  border: solid 1px #93533c;
  border-radius: 16px;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}
.cat-parent {
  padding: 0px 15px 0px 15px;
}

.cat-text {
  position: absolute;
  bottom: 0px;
  background: white;
    padding-left: 5px;
    padding-right: 5px;
  border-radius: 0 5px 0 0;
  font-family: cursive;
  color: #93533c;
  font-size: 1.5rem;
  font-weight: 600;
  font-family: gifthub-font;
}


@keyframes fade {
  from {opacity: .4}
  to {opacity: 1}
}