
.testim {
    padding: 0 15px 0 15px;
    text-align: center;
    margin-top: 25px;
    font-family: 'gifthub-font';
    color: #93533c;
}

.testim-heading {
    font-size: 2rem;
    font-weight: bold;
    margin-bottom: 15px;;
}
.testim-sub-heading {
    font-size: 1.5rem;
    font-weight: 700;
    text-decoration: underline;
    font-family: 'gifthub-font-1'
}

.testim-container {
    background-color: #93533c;
    color: white;
    flex-direction: column;
    padding: 10px 0px 10px 0px;
    font-family: gifthub-font;
    font-size: 1.2rem;
    border-radius: 10px;
}

q {
font-family:  Georgia, "Times New Roman", Times, serif;
font-size: 50px;
}

q::before {
    content: open-quote;
}
q::after {
    content: no-close-quote;
}
.testim-content {
    margin-top: -20px;
}

h3 {
  padding: 10px 0px 10px 0px;  
}

.testim-content-container {
    position: relative;
    min-height: 160px;
}
.main-content {
    position: absolute;
    height: 100%;
    width: 100%;
    transition: left 1s;
    font-family: 'gifthub-font';
}

@keyframes slideIn {
    0%   {left: 100%;}
    50%  {left: 50%;}
    100% {left: 0%;}
  }
  @keyframes slideOut {
    0%   {left: 0%;}
    50%  {left: 50%;}
    100% {left: 100%;}
  }
  