

.cat-box-outer {
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
    display: flex;
    flex-direction: column;
    flex: 1;
    border-radius: 8px;
    border: 1px solid #cbd5e1;
    max-width: 50%;
    min-width: 45%
}

.cat-box-inner {
    flex-direction: column;
    display: flex;
    
}

.cat-img-container {
    /* width: 350px; */
    display: flex;
}

.cat-img-container  img {
   min-width: 100%;
   height: 200px; 
   border-radius: 8px 8px 0px 0px;
   object-fit: cover;
}

.cat-info {
    display: flex;
    flex-direction: column;
    padding-left: 15px;
    gap: 7px;
    padding-bottom: 10px;
    padding-top: 10px;
}

.product-name {
    color: var(--primary-color);
    font-size: 12px;
    text-overflow: ellipsis;
    max-width: 80%;
    overflow: hidden;
    white-space: nowrap;
}

.product-price {
    color: black;
    font-weight: bold;
}

.prduct-main-info {
    padding-bottom: 20px;
}

.product-details  .name {
    display: flex;
    font-size: 24px;
    padding-left: 15px;
    margin-top: 10px;      
    margin-bottom: 5px;
}

.product-details .price {
    font-size: 18px;
    font-weight: bolder;
    padding-left: 15px;
    margin-top: 5px;
    color: var(--primary-color);
}

.product-seperator {
    display: flex;
    background-color: #f6f6f6;
    height: 8px;
}

.pay-container {
    display: flex;
    position: absolute;
    bottom: 0;
    height: 50px;
    text-align: center;
    justify-content: center;
    background-color: var(--primary-color);
    width: 100%;
}

.wp-icon {

}

.wp-txt {
    font-size: 20px;
    font-weight: bold;
    color: white;
    align-items: center;
    display: flex;
}